
import ProjectsSection from './ProjectsSection.vue';
import Contact from './Contact.vue';
import PresentationSection from './PresentationSection.vue';
import SkillsSection from './SkillsSection.vue';
import DarkLightToggle from './helper/DarkLightToggle.vue';
import LanguageToggle from './helper/LanguageToggle.vue';
import { onBeforeUnmount,defineComponent, onMounted, computed, ComputedRef } from 'vue';
import { useStore } from 'vuex';
import { Content, Link, LocaleString, Section } from '@/store';


export default defineComponent({
  props: {
  },
  components: {
    PresentationSection,
    Contact,
    ProjectsSection,
    SkillsSection,
    DarkLightToggle,
    LanguageToggle,
  },
  data() {
    return {
      inMove: false,
      offsets: [] as number[],
      windowTop: 0
    };
  },
  methods:{

    getDescriptions(){
      return this.content?.description?.map((description: LocaleString)=> description.fr);
    },
    getSections(){
      if (this.section){
        return [this.section.about.fr, this.section.skill.fr,
               this.section.project.fr, this.section.contact.fr]
      }
      return ['About', 'Skills', 'Projets', 'Contact'];
    },
    openUrl(){
      window.open(this.link.cv, '_blank')?.focus()
    },
    getSectionIndex(){
      for( let i =0;i<this.offsets.length;i++){
        if (this.offsets[i] > this.windowTop){
          return i-1;
        }
      }
      return this.offsets.length -1;
    },
     scrollToSection(id:any, force = false){
      if (this.inMove && !force) return false;

      this.inMove = true;
      document.getElementsByTagName('section')[id].scrollIntoView({ behavior: 'smooth' });
      setTimeout(() => {
        this.inMove = false;
      }, 600);
    }
  },
 
  setup(){
    const store = useStore();
    const section : ComputedRef<Section> = computed(()=> store.getters.section);
    const content : ComputedRef<Content> = computed(()=> store.getters.content);
    const link : ComputedRef<Link> = computed(()=> store.getters.link);
    onMounted(()=>{
      store.dispatch("FetchSection", 1);
      store.dispatch("FetchContent", 1);
      store.dispatch("FetchLink", 1);
    })
    return {
      section,content,link
    }
  },
  mounted() {
    const body = document.querySelector('body') as HTMLElement;
    body.setAttribute('style', 'margin:0;');

    const calculateSectionOffsets = () => {
      const sections = document.getElementsByTagName('section');
      this.offsets = [];
      const length = sections.length;
      for (let i = 0; i < length; i++) {
        const sectionOffset = sections[i].offsetTop - 100;
        this.offsets.push(sectionOffset);
      }
    };
    const onScroll = (e:any) => {
      this.windowTop = window?.top?.scrollY as number; /* or: e.target.documentElement.scrollTop */
      //this.windowTop = window?.top?.scrollY || e.target.documentElement.scrollTop;
    };
      calculateSectionOffsets();
      window.addEventListener('scroll', onScroll);
      window.addEventListener('resize', calculateSectionOffsets);
 

    onBeforeUnmount(() => {
      window.removeEventListener('scroll', onScroll);
      window.removeEventListener('resize', calculateSectionOffsets);
    });
  }
});
